<template>
  <div class="schedule">
    <table class="elevation-1">
      <thead>
        <tr>
          <th class="horario primary">Hora</th>
          <th
            :key="index"
            v-for="(scheduleDate, index) in schedulesDate"
            :width="`calc(100% / ${schedulesDate.length})`"
            class="text-center primary"
          >
            <span class="d-block font-weight-thin">{{ scheduleDate.date | dayOfWeek }}</span>
            <span class="d-block">{{ scheduleDate.date | dayOfDate }}</span>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          :key="index"
          v-for="(time, index) in timesModel"
        >
          <td class="horario">{{ time.text }}</td>
          <td
            :key="index"
            v-for="(schedule, index) in schedulesModel"
            class="text-center"
          >
            <div class="scheduled" v-if="isScheduled(schedule, time.text)">
              <img
                v-if="isScheduled(schedule, time.text).user.image_url"
                :src="getImage(isScheduled(schedule, time.text).user.image_url)" 
                class="image"
              />
              <span class="d-block username">
                {{ isScheduled(schedule, time.text).user.name }}
              </span>
            </div>
            <div
              class="free"
              :class="{ finished: isFinished (schedule, time.text) }"
              v-else
            >
              LIVRE
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Axios from '@/plugins/axios'
import DateMixin from '@/mixins/DateMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import TimeMixin from '@/mixins/TimeMixin'

export default {
  name: 'Schedule',
  mixins: [ DateMixin, SnackbarMixin, TimeMixin ],
  data: () => ({
    startDate: null,
    endDate: null,
    barberSchedules: [],
    loading: true,
    startTime: null,
    endTime: null
  }),
  filters: {
    dayOfDate (date) {
      return date.split('-')[2]
    }
  },
  computed: {
    schedulesDate () {
      if (this.startDate && this.endDate) {
        let dates = []
        let startDate = new Date(this.startDate)
        startDate.setDate(startDate.getDate() + 1)
        let endDate = new Date(this.endDate)
        endDate.setDate(endDate.getDate() + 1)
        for (let d = startDate; d <= endDate; d.setDate(d.getDate() + 1)) {
          dates.push({ date: this.formatDateToDateEn(d) })
        }
        return dates
      }
      return []
    },
    schedulesModel () {
      let schedules = [...this.schedulesDate]
      schedules.forEach(schedule => {
        schedule.items = this.barberSchedules.filter(barberSchedule => {
          const date = barberSchedule.start_date.split(' ')[0]
          return date === schedule.date
        })
      })
      return schedules
    },
    timesModel () {
      return this.times.filter(time => {
        return time.text >= this.startTime && time.text <= this.endTime
      })
    }
  },
  methods: {
    getImage (image) {
      return `${this.$url_backend}${image}`
    },
    getSchedules (barberId, startDate, endDate, startTime, endTime) {
      this.startTime = startTime
      this.endTime = endTime
      this.startDate = startDate
      this.endDate = endDate
      this.loading = true
      Axios.get(`/barber/${barberId}/schedules?start_date=${startDate}&end_date=${endDate}`)
      .then(response => {
        if (Number(response.status) === 200 && Number(response.data.codigo) === 200) {
          this.barberSchedules = response.data.corpo
          this.loading = false
        } else this.setError(response.data.corpo)
      })
      .catch (() => this.setError())
    },
    isFinished (schedule, time) {
      return Date.now() > (new Date(`${schedule.date} ${time}`)).getTime()
    },
    isScheduled (schedule, time) {
      if (schedule.items.length > 0) {
        return schedule.items.find(s => {
          const startDate = s.start_date.split(' ')[1]
          const endDate = s.end_date.split(' ')[1]
          time = `${time}:00`
          return startDate <= time && endDate > time
        })
      }
      return false
    },
    saveSchedule (scheduleDate, time) {
      window.console.log(scheduleDate, time)
    },
    setError (error = 'No foi possvel carregar a agenda!') {
      this.barberSchedules = []
      this.loading = false
      this.showSnackbarErro(error)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/scss/mixin.scss';
@import '@/scss/theme.scss';

.schedule { overflow: hidden; overflow-x: auto; }
table { border-spacing: 0; background: #fff; width: 100%; border-radius: 10px 10px 0 0; min-width: 100%; }
thead tr th:first-child { border-radius: 10px 0 0 10px; }
thead tr th:last-child { border-radius: 0 10px 10px 0; }
th, td { text-align: center; padding: 10px 15px; }
th { color: #fff; height: 60px; font-size: 1rem; }
td { border-bottom: 1px solid $primaryLight; }
tbody tr:last-child td { border-bottom: none; }
td.horario { color: #919191; font-weight: 600; border-right: 1px solid $primaryLight; }
.horario { width: 60px; }
.free { color: #1FC4C5; font-weight: bold; 
  &.finished { color: #919191; }
}
.scheduled {
  color: #242424;
  font-weight: 500;
  padding: 10px 0;
  cursor: pointer;
  border-radius: 5px; 
  &:hover { background: #cccccc80; @include box-shadown-mixin (0.5) }
}
.username { font-size: .8rem; font-weight: 600; }
.image { width: 60px; border-radius: 50%; border: 2px solid #000; }
</style>