<template>
  <div class="container mt-4">
    
    <schedule-filter
      :filters="filters"
      @filter="filter"
    />
    <app-schedule
      ref="schedules"
      class="mt-6"
    />
  </div>
</template>

<script>
import AppSchedule from '@/components/admin/Schedule'
import ScheduleFilter from '@/components/admin/ScheduleFilter'
import BarberMixin from '@/mixins/BarberMixin'

export default {
  name: 'Schedule',
  components: { AppSchedule, ScheduleFilter },
  mixins: [ BarberMixin ],
  data: () => ({
    filters: null
  }),
  mounted () {
    const filters = window.localStorage[`admin-schedule-filters-${ this.barber.id }`]
    if (filters) this.filters = JSON.parse(filters)
  },
  methods: {
    filter (barberId, dates, startTime, endTime) {
      const key = `admin-schedule-filters-${ this.barber.id }`
      window.localStorage[key] = JSON.stringify({ barberId, dates, startTime, endTime })
      this.$refs.schedules.getSchedules(barberId, dates[0], dates[1], startTime, endTime)
    }
  }
};
</script>
