<template>
  <div class="schedule-filter">
    <div class="schedule-input pr-0 pr-sm-2 pr-md-0">
      <date-picker-range
        id="dates"
        label="Perodo:"
        :icon="null"
        :outlined="true"
        :dense="true"
        :value="dates"
        :min="minDate"
        :max="maxDate"
        class="white"
        :hideDetails="true"
        cssClass="white"
        @change="setData"
      />
    </div>
    <div class="schedule-input time-field ml-0 ml-md-4 pl-0 pl-sm-2 pl-md-0 mt-4 mt-sm-0">
      <select-field
        id="startTime"
        label="Horrio inicial:"
        icon="schedule"
        :value="startTime"
        :outlined="true"
        :items="times"
        :hideDetails="true"
        class="white"
        @onChange="setData"
      />
    </div>
    <div class="schedule-input time-field mt-4 mt-md-0 ml-0 ml-md-4 pr-0 pr-sm-2 pr-md-0">
      <select-field
        id="endTime"
        label="Horrio final:"
        icon="schedule"
        :value="endTime"
        :outlined="true"
        :items="times"
        :hideDetails="true"
        class="white"
        @onChange="setData"
      />
    </div>
    <div class="schedule-input ml-0 ml-md-4 mt-4 mt-md-0 pl-0 pl-sm-2 pl-md-0">
      <select-field
        id="barber"
        label="Barbeiros:"
        :value="barber"
        :outlined="true"
        :items="barbers"
        :hideDetails="true"
        class="white"
        @onChange="setData"
      />
    </div>
  </div>
</template>

<script>
import Axios from '@/plugins/axios';
import DatePickerRange from '@/components/globais/form/DatePickerRange';
import SelectField from '@/components/globais/form/select-field/SelectField';
import BarbershopMixin from '@/mixins/BarbershopMixin'
import DateMixin from '@/mixins/DateMixin'
import TimeMixin from '@/mixins/TimeMixin'

export default {
  name: 'ScheduleFilter',
  components: { DatePickerRange, SelectField },
  mixins: [ BarbershopMixin, DateMixin, TimeMixin ],
  props: { filters: { default: null } },
  data: () => ({
    barber: null,
    barbers: [],
    dates: [],
    startTime: null,
    endTime: null
  }),
  watch: {
    filters (val) {
      if (val) this.setFilters(val)
    }
  },
  computed: {
    maxDate() {
      if (!this.dates[0] || this.dates[1]) return null;
      let date = new Date(this.dates[0]);
      date.setDate(date.getDate() + 7);
      return this.formatDateToDateEn(date);
    },
    minDate() {
      if (!this.dates[1]) {
        let date = new Date(this.dates[0]);
        date.setDate(date.getDate() + 1);
        return this.formatDateToDateEn(date);
      }
      return null;
    },
  },
  mounted () {
    if (this.filters) {
      this.setFilters(this.filters)
    } else {
      const today = new Date();
      const endDate = new Date()
      endDate.setDate(endDate.getDate() + 6)
      this.dates = [
        this.formatDateToDateEn(today),
        this.formatDateToDateEn(endDate),
      ];
    }
    this.getBarbers();
  },
  methods: {
    setData(value, id) {
      this[id] = value
      this.filter()
    },
    filter () {
      if (this.barber && this.dates[0] && this.dates[1]) {
        this.$emit('filter', this.barber, this.dates, this.startTime, this.endTime)
      }
    },
    getBarbers() {
      Axios.get(`/barbershop/${this.barbershop.id}/barber`)
      .then((response) => {
        if (response.status === 200 && response.data.codigo === 200) {
          this.barbers = response.data.corpo.map(barber => {
            return {
              text: barber.name,
              value: barber.id
            }
          })
        }
      })
    },
    setFilters (filters) {
      this.barber = filters.barberId
      this.dates = filters.dates
      this.startTime = filters.startTime
      this.endTime = filters.endTime
    }
  }
};
</script>

<style scoped>
.schedule-filter { display: flex; flex-wrap: wrap; }
.schedule-input { width: 250px; }
.time-field { width: 150px; }
@media all and (max-width: 959px) {
  .schedule-input { width: 50%; }
}
@media all and (max-width: 599px) {
  .schedule-input { width: 100%; }
}
</style>
